<template>
  <div class="index">
    <app-header>
      <span slot="left"></span>
      <span slot="title"></span>
      <template slot="right">
        <span class="icon icon-search" @click="show"></span>
      </template>
    </app-header>
    <main-panel :appList="appList" :showActive="showActiveFlag" :pageRole="pageRole"></main-panel>
    <div class="index-search-panel" v-show="showFlag">
      <div class="header">
        <span class="icon-box"><i class="icon icon-search"></i></span>
        <input class="input" type="text" />
        <a class="cancel" href="javascript:;" @click="hide"></a>
      </div>
      <div class="recommend">
        <div>
          <h2 class="title"></h2>
          <div class="content">
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import appHeader from '@/components/appHeader'
import mainPanel from '@/components/mainPanel'
import axios from 'axios'

export default {
  name: 'index',
  components: {
    appHeader,
    mainPanel
  },
  data () {
    return {
      appList: {},
      showFlag: false,
      showActiveFlag: true,
      pageRole: 'index'
    }
  },
  created () {
    axios.get('static/mocks/index/data.json').then((res) => {
      this.appList = res.data
    })
  },
  methods: {
    show () {
      this.showFlag = true
    },
    hide () {
      this.showFlag = false
    }
  }
}
</script>

<style lang="scss">
@import "../../assets/scss/md";
.index {
  .index-search-panel {
    position: fixed;
    top: 0;
    left: 0;
    padding: 0 10px;
    width: 100%;
    height: 100%;
    background: $bgColor;
    z-index: $zIndexOperation;
    >.header {
      display: flex;
      width: 100%;
      height: 38px;
      line-height: 38px;
      border-bottom: 1px solid #ccc;
      >.icon-box {
        @include centerH();
        width: 10%;
      }
      >.input {
        flex: 1;
        outline: none;
      }
      >.cancel {
        width: 10%;
      }
    }
    >.recommend {
      position: absolute;
      top: 0;
      left: 0;
      padding: 38px 0;
      width: 100%;
      height: 100%;
      @include centerH();
      z-index: -1;
      text-align: center;
      .title {
        margin: 0 auto;
        width: 40%;
        height: 38px;
        line-height: 38px;
        color: #ddd;
        border-bottom: 1px solid #ddd;
      }
      .content {
        margin-top: 30px;
        color: $fontColor;
        line-height: 38px;
      }
    }
  }
}
</style>



